import React, { Component } from 'react';
import ContactButtons from './ContactButtons';

class Header extends Component {
  render() {
    return (
      <div id="header" className="text-center front-div mx-auto">
        <img
          src="img/professional_pic_headshot.png"
          alt="Me - Christopher McIntosh"
          id="headshot-pic"
          className="front-div"
        />
        <h2 className="font-weight-bold">Christopher McIntosh</h2>
        <h5>Full Stack Developer</h5>
        <h5>New York, NY</h5>
        <div className="pb-4">
          <ContactButtons />
        </div>
      </div>
    );
  }
}

export default Header;



