import React, { Component } from 'react';

class About extends Component {
  render() {
    return (
      <div id="about" className="outer gray">
        <div className="inner gray mx-auto">
          <h2 className="font-weight-bold">About</h2>
          <p>
            I'm a <span className="font-weight-bold">Full Stack</span> developer
            specialized in both
            <span className="font-weight-bold"> Front-end</span> and
            <span className="font-weight-bold"> Back-end</span> development.
          </p>
          <br />
          <p>
            I graduated from Lehman College with a bachelor degree in Computer
            Science in 2015.
          </p>
          <br />
          <p>
            Being in a technology field with a subject that is always changing,
            I decided I wanted to improve my programming skills by enrolling
            into Fullstack Academy in Fall 2017.
          </p>
        </div>
      </div>
    );
  }
}

export default About;



