import React, { Component } from 'react';
import FontAwesomeIcon from './FontAwesomeIcon';

class ContactButtons extends Component {
  render() {
    return (
      <div className="icons">
        <a href="mailto:chrismac92@gmail.com">
          <FontAwesomeIcon icon={['fas', 'envelope-square']} />
        </a>
        <a
          href="https://github.com/christophermcintosh"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={['fab', 'github-square']} />
        </a>
        <a
          href="https://www.linkedin.com/in/christopher-mcintosh/"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={['fab', 'linkedin']} />
        </a>
      </div>
    );
  }
}

export default ContactButtons;



